import React from 'react';
import './homepage.scss';
import Hero from '../../components/hero/hero.cmp';
import ItemHome from '../../components/item-home/item-home.cmp';
import { FullPage, Slide } from 'react-full-page';

class HomePage extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			sections: [
				{
					id: 1,
					title: 'Flowers',
					subtitle: 'Flower Blossom ',
					imageUrl: 'https://res.cloudinary.com/sara-cloud/image/upload/v1630741351/Flowers/IMG20210530104214_jhlltg.jpg',
					revield: false
				},
				{
					id: 2,
					title: 'Rain',
					subtitle: 'Rain Drops',
					imageUrl: 'https://res.cloudinary.com/sara-cloud/image/upload/v1630736907/HomeScreen/rain_ytpxft.jpg',
					revield: false
				},
				{
					id: 3,
					title: 'Pets',
					subtitle: 'Pet Love',
					imageUrl:
						'https://res.cloudinary.com/sara-cloud/image/upload/v1630736907/HomeScreen/pets_n4zcpx.jpg',
					revield: false
				},
				{
					id: 4,
					title: 'AroundMe',
					subtitle: 'Some Magic',
					imageUrl: 'https://res.cloudinary.com/sara-cloud/image/upload/v1630736906/HomeScreen/casual_yzewzp.jpg',
					revield: false
				},
				{
					id: 5,
					title: 'Prince',
					subtitle: 'Beautiful Memories',
					imageUrl: 'https://res.cloudinary.com/sara-cloud/image/upload/v1632295287/HomeScreen/IMG20210102144403_ajr2pj.jpg',
					revield: false
				}
			]
		};
	}

	handleWaypoint = (index, action) => {
		this.setState((state) => {
			const list = state.sections.map((item, i) => {
				if (index === i) {
					if (action === 'enter') {
						return (item.revield = true);
					} else if (action === 'leave') {
						return (item.revield = false);
					}
				}
				return item;
			});
			return {
				list
			};
		});
	};

	render() {
		return (
			<FullPage className="fullpage-wrapper">
				<Slide>
					<Hero />
				</Slide>
				{this.state.sections.map((item, index) => (
					<Slide key={item.id}>
						<ItemHome item={item} handleWaypoint={this.handleWaypoint} reviel={this.state.sections} index={index} />
					</Slide>
				))}
			</FullPage>
		);
	}
}

export default HomePage;
