import React from 'react';
import './about.scss';
import { Waypoint } from 'react-waypoint';
import { Link } from 'react-router-dom';
import { FaInstagram } from 'react-icons/fa';
import { FaGithub } from 'react-icons/fa';

class AboutPage extends React.Component {
	constructor() {
		super();
		this.state = {
			revield: false
		};
	}

	handleWaypoint = () => {
		this.setState((prevState) => ({
			revield:true
		}));
	};

	render() {
		
		return (
			<div className="about flex-c">
				
				<div className={`letters ${this.state.revield ? 'show' : ''}`}>SARA.</div>
				<div className="backround" />
        <div className={`about-text flex-c ${this.state.revield ? 'show' : ''}`}>
        <Waypoint onEnter={this.handleWaypoint} />
					<h1 className="title">About me</h1>
					<h2>I'm a mobile photographer who loves to capture beautiful things.</h2>
					<p>
					I started doing mobile photography recently. 
					I'm using my smartphone(Oppo Reno 3 Pro) that gives me full controll of the scenery and opportunity for more exiting pictures.
					</p>
					<div className="about-social flex-c">
						<Link className="link-button" to="/contact">
							Contact me
						</Link>
						<a href="https://www.instagram.com/sachinsara200/" target="blank">
							<FaInstagram className="react-icons" />
						</a>
						<a href="https://github.com/SaravanakumarVijay" target="blank">							
							<FaGithub className="react-icons" />
						</a>
					</div>
				</div>
				<div className="about-image-area flex-c">
					<img
						src="https://res.cloudinary.com/sara-cloud/image/upload/v1650464380/male-vlogger-4821733-4018626_gvt5t5.png"
						className={`about-img ${this.state.revield ? 'show' : ''}`}
						alt="iliyan-portrait"
					/>
				</div>
			</div>
		);
	}
}

export default AboutPage;
